import * as React from 'react';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import BackspaceIcon from '@mui/icons-material/Backspace';

// 

class Display extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      digits: [],
      fruit: "",
      enter: false,
      tries: 6,
      won: false
    }
    updateDigits = updateDigits.bind(this)
    checkAnswer = checkAnswer.bind(this)
  }

  componentDidMount() {
    var moment = require('moment-timezone');
    var newYork = moment.tz(new Date(), "America/New_York").subtract(12, 'hours');
    var seed = newYork.format('YYYYMMDD');

    // Local PRNG: does not affect Math.random.
    var seedrandom = require('seedrandom');
    var rng = seedrandom(seed);
    var kiwi = Math.floor(rng() * 100)
    console.log(kiwi)
    this.setState({kiwi: kiwi});
  }

  render() {

    let digit1, digit2 = "";
    if (this.state.digits[0] !== undefined) {
      digit1 = this.state.digits[0];
    }

    if (this.state.digits[1] !== undefined) {
      digit2 = this.state.digits[1];
    }

    let info = this.state.tries
    if (this.state.won) {
      info = "You found the kiwi!"
    }
    else if (this.state.tries == 0) {
      info = "You didn't find the kiwi!"
    }

    return (
      <Grid container spacing={2} sx={{ mb: 3 }}>
        <Grid item xs={12}><Typography variant="secondary" align="left">{info}</Typography></Grid>
        <Grid item xs={12}><Typography variant="h2" align="center" sx={{ height: 70 }}>{this.state.fruit}</Typography></Grid>
        <Grid item xs={3}></Grid>
        <Grid item xs={3}>
          <Typography variant="h2" align="center" sx={{ borderBottom: 2, height: 70 }}>{digit1}</Typography>
        </Grid>
        <Grid item xs={3}>
          <Typography variant="h2" align="center" sx={{ borderBottom: 2, height: 70 }}>{digit2}</Typography>
        </Grid>
        <Grid item xs={3}></Grid>
      </Grid>
    );
  }
}

function checkAnswer() {
  if (!this.state.enter && !this.state.won && this.state.digits.length == 2 && this.state.tries > 0) {

    this.setState({ enter: true })
    let answer = parseInt(this.state.digits[0] + this.state.digits[1])
    let diff = Math.abs(this.state.kiwi - answer)
    let fruit = ""
    let won = false
    if (diff > 21) {
      fruit = ""
    }
    else if (diff > 8) {
      fruit = ""
    }
    else if (diff > 3) {
      fruit = ""
    }
    else if (diff > 1) {
      fruit = ""
    }
    else if (diff == 1) {
      fruit = ""
    }
    else if (diff == 0) {
      fruit = ""
      won = true
    }

    this.setState({fruit: fruit, tries: this.state.tries - 1, won: won})
  }
}

function updateDigits(digit) {
  /*
    if this.state.digits.length < 2
      state.digits.append(digit)
    
  */
  if (!this.state.won && this.state.tries > 0) {
    if (digit == 'BKSP') {
      if (this.state.digits.length > 0) {
        let updated = this.state.digits
        updated.pop();
        this.setState({ digits: [...updated], enter: false})
      }
    }
    else if (this.state.digits.length < 2) {
      let updated = this.state.digits
      updated.push(digit)
      this.setState({ digits: [...updated]})
    }
    else if (this.state.enter) {
      this.setState({ digits: [digit], enter: false })
    }
  }
}

function Numpad() {

  return (
    <Grid container spacing={1}>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray"
        onClick={(e) => updateDigits(e.target.innerText)}>1</Button>
      </Grid>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray"
        onClick={(e) => updateDigits(e.target.innerText)}>2</Button>
      </Grid>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray"
        onClick={(e) => updateDigits(e.target.innerText)}>3</Button>
      </Grid>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray"
        onClick={(e) => updateDigits(e.target.innerText)}>4</Button>
      </Grid>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray"
        onClick={(e) => updateDigits(e.target.innerText)}>5</Button>
      </Grid>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray"
        onClick={(e) => updateDigits(e.target.innerText)}>6</Button>
      </Grid>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray"
        onClick={(e) => updateDigits(e.target.innerText)}>7</Button>
      </Grid>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray"
        onClick={(e) => updateDigits(e.target.innerText)}>8</Button>
      </Grid>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray"
        onClick={(e) => updateDigits(e.target.innerText)}>9</Button>
      </Grid>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray" startIcon={<BackspaceIcon />} 
        onClick={(e) => updateDigits("BKSP")}>&nbsp;</Button>
      </Grid>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray"
        onClick={(e) => updateDigits(e.target.innerText)}>0</Button>
      </Grid>
      <Grid item xs={4}>
        <Button variant="contained" sx={{ width: 1 }} size="large" color="gray"
        onClick={(e) => checkAnswer()}>ENTER</Button>
      </Grid>
    </Grid>
  );
}

export default function App() {
  return (
    <Container maxWidth="sm">
      <Box sx={{ my: 4 }}>
        <Display />
        <Numpad />
      </Box>
    </Container>
  );
}
